@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@300;500&display=swap');

:root {
    --gap-s: 12px;
    --gap-m: 16px;

    --border-radius-s: 8px;
    --border-radius-l: 16px;

    --text: #333;
    --text-secondary: #666;

    --bg-secondary: #f7f7f7;
    --bg-primary: #0054ff2b;
}

.voice {
    height: 20px;
    width: 20px;
}

body {
    font-family: 'Raleway', sans-serif;
    margin: 0;
    padding: var(--gap-m);
    color: var(--text);
}

.root {
    display: flex;
    flex-direction: column;
    height: 100%;
}

.title {
    text-align: center;
    width: 100%;
    font-size: 2em;
    margin: 0;
}

.input {
    padding: var(--gap-s);
    border-radius: var(--border-radius-l);
    margin-top: var(--gap-m);
    color: var(--text-secondary);
    position: sticky;
    bottom: var(--gap-m);
    background: var(--bg-secondary);

    outline: none !important;
    border-bottom-right-radius: 0;
}

.messages {
    margin-top: auto;
}

.message {
    position: relative;
    display: flex;
    padding: var(--gap-s);
    border-radius: var(--border-radius-l);
    color: var(--text-secondary);
    background: var(--bg-secondary);
    &:not(:last-child){
         margin-bottom: var(--gap-s);
    }
}

.messageFrom {
    margin-left: auto;
}
.messageFrom, .messageTo {
    max-width: 85%;
    width: fit-content;
}

.messageBody {
    composes: message;
}

.messageFrom .messageBody {
    background: var(--bg-primary);
    border-bottom-right-radius: 0;
}

.messageTo .messageBody {
    border-bottom-left-radius: 0;
}
