.row {
    display: flex;
    align-items: center;
    width: 100%;
    margin: var(--gap-s) auto;
    transition: transform 0.2s ease-in-out;
}

.item {
    margin: 0 var(--gap-s);
    padding: var(--gap-s);
    font-family: inherit;
    font-size: 16px;
    font-weight: bold;
    border-radius: var(--border-radius-s);
    color: var(--text-secondary);
    white-space: nowrap;
    cursor: pointer;
    transition: width .2s ease-in-out;
}

.itemActive {
    opacity: 1;
    width: 90%;
    color: var(--text);
    text-align: center;
    background: var(--bg-secondary);
}